*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  font-family: system-ui, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  line-height: 1.15;
}

body {
  margin: 0;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  border-color: currentColor;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --text-primary: #050505;
  --text-secondary: #05050580;
  --popular-description-text-color: #050505cc;
  --text-secondary-light: #0505054d;
  --text-secondary-light-hover: #05050580;
  --text-accent: #9bb537;
  --text-btn-primary: #f8f8f8;
  --text-recipe-card-title: #f8f8f8;
  --text-recipe-card-text: #f8f8f8;
  --hover-color: #9bb537;
  --active-color: #7f9e2c;
  --bg-primary: #f8f8f8;
  --mobile-menu-bg: #9bb537;
  --mobile-menu-text: #f8f8f8;
  --mobile-menu-current: #fff;
  --modal-bg: #fff;
  --input-border: 1px solid #05050533;
  --btn-primary-bg: #9bb537;
  --slide-card-bg: #050505;
  --slide-card-text-primary: #f8f8f899;
  --slide-card-text-location: #f8f8f833;
  --all-categories-scrollbar-color: #0505052e;
  --ingredient-underline-color: #0505051a;
  --tag-border: 1px solid #05050533;
  --star-icon-base-color: #80808080;
  --star-icon-highlighted-color: #eea10c;
  --star-icon-highlighted-color-light: #eea10c4d;
  --pagination-btn-outline: #0505054d;
  --pagination-btn-left-icon-color: #f8f8f880;
  --up-to-top-icon-color: #00000080;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, li {
  list-style: none;
}

img, video, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  width: 100%;
  height: 100%;
}

html {
  scroll-behavior: smooth;
  scrollbar-gutter: stable;
  font-family: Inter, sans-serif;
  font-weight: 500;
}

body {
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: all .3s ease-out;
  overflow-y: auto;
}

body.gscrollbar-fixer {
  margin-right: 0 !important;
}

.current {
  color: var(--text-accent);
}

.mobile-menu-current {
  color: var(--mobile-menu-current);
  font-weight: 600;
}

.no-scroll {
  overflow: hidden;
}

button {
  font: inherit;
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  display: inline-block;
}

button, a, input, textarea, li, select, label {
  box-shadow: none;
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

button:focus, a:focus, input:focus, textarea:focus {
  outline: none;
}

button:focus-visible, a:focus-visible, input:focus-visible, textarea:focus-visible {
  outline: 2px solid var(--hover-color);
  outline-offset: 2px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

