@use '../../utils/mixins' as *;

.popular-recipes__img-wrapper {
  width: 64px;
  height: 64px;
  overflow: hidden;
  border-radius: 8px;

  @include media(md) {
    width: 48px;
    height: 48px;
  }

  @include media(lg) {
    width: 64px;
    height: 64px;
  }
}

.popular-recipes__img {
  @include transition;
}
